body {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

textarea {
  outline: none;
  margin-bottom: 10px;
  padding: 10px;
  border-radius: 4px;
}

button {
  display: block;
  border-radius: 4px;
  outline: none;
  margin-bottom: 10px;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.result {
  font-size: 14px;
  color: gray;
}
